"use client";

import React, { useState } from "react";
import Header2 from "@/Navbar/Header2";

function Header() {
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <header id="mad-header" className="mad-header header-2">
      <div className="mad-header-section--sticky-xl">
        <div className="container">
          <div className="row align-items-center">
            {/* Logo */}
            <div className="col-xl-3 col-lg-4 col-sm-3 col-6">
              <a href="/" className="mad-logo">
                <img src="/img/logo.png" alt="Logo" />
              </a>
            </div>

            {/* Use existing toggle button */}
            <div className="col-6">
              <button className="mad-cat-toggle hello"></button>
            </div>

            {/* Navigation */}
            <div
              className={`col-xl-6 col-lg-4 col-sm-12 ${
                menuOpen ? "menu-open" : "menu-closed"
              }`}
            >
              <nav className="mad-navigation-container">
                <ul className="mad-navigation mad-navigation--vertical-sm">
                  <li className="menu-item current-menu-item">
                    <a href="#">Home</a>
                  </li>
                  <li className="menu-item">
                    <a href="#">Shop</a>
                  </li>
                  <li className="menu-item">
                    <a href="#">Pages</a>
                  </li>
                  <li className="menu-item">
                    <a href="#">Elements</a>
                  </li>
                  <li className="menu-item">
                    <a href="#">Blog</a>
                  </li>
                </ul>
              </nav>
            </div>

            {/* Right Icons */}
            <div className="col-xl-3 col-lg-4 col-sm-6 d-none d-lg-block">
              {/* ... your support + icons code ... */}
            </div>
          </div>
        </div>
      </div>

      {/* Browse categories + search */}
      <Header2 />
    </header>
  );
}

export default Header;
